import React from 'react';
import { Link } from 'react-router-dom';
import './About.css';

const About = () =>(
        <div className="burgar">
            <aside>
            <h1>About</h1>
            <h4>徳島音美について</h4><h3></h3>
            </aside>
            <div class="aboutOnbi">
            <h2>音美</h2><br/>
            <br/>
            音美とは中・四国国立大学音楽美術連盟の略称のことです。<br/>
            音美では毎年12月上旬頃に中・四国の(旧を含む)国立大学が集まり、<br/>
            演奏会や展覧会を催しています。<br/>
            <br/>
            <h2>音楽部門</h2><br/>
            <br/>
            音楽部門では吹奏楽、邦楽、声楽、ギター、マンドリンの各大学個別演奏や、<br/>
            大規模であったり、企画であったりとバリエーション豊かな合同演奏があります。<br/>
            音楽部門は二日間にわたって開催され、一日目に邦楽、声楽、ギター、マンドリン部門、<br/>
            二日目に吹奏楽、オーケストラ部門が行われます。<br/>
            毎年20団体以上が参加するとても規模の大きい演奏会となっています。<br/>
            <br/>
            <h2>美術部門</h2><br/>
            <br/>
            美術部門では各大学の美術部、写真部、書道部、陶芸部などに所属する方々によって<br/>
            創作されたさまざまな作品を展示する美術展覧会を行います。<br/>
            <br/>
            <h2>音美のはじまり</h2><br/>
            <br/>
            音美は昭和29年（1954年）に始まりました。<br/>
            これは、戦後間もない頃、1団体独力でコンサートや展覧会を開くことが困難だった時代に、<br/>
            中・四国の国立大学が寄り添って協力し合うことで誕生した演奏会・展覧会でした。<br/>
            現在では、「学生の音楽美術に関する課外活動を促進し、地域文化の健全な発展に寄与するとともに<br/>
            加盟大学学生相互の親睦を図ること」（中・四国国立大学音楽美術連盟規約より抜粋）が音美の主要な開催理由として挙げられます。<br/>
            <br/>
            <h2>徳島音美</h2><br/>
            <br/>
            開催地は中・四国の旧国立大学（現国立大学法人）である、<br/>
            島根大学、山口大学、岡山大学、広島大学、香川大学、愛媛大学、徳島大学、高知大学、鳥取大学が順番に受け持っているのですが、<br/>
            2019年の音美の当番大学が徳島大学となり、徳島県で開催されます。<br/>
            よって、2019年に開かれる音美を通称「徳島音美」 といいます。<br/>
            ちなみに、2017年は鳥取大学、2018年は高知大学がそれぞれ当番大学を務めました。<br/>
            </div>
            <input type="checkbox" id="menu-toggle"/>
            <label id="trigger" for="menu-toggle"></label>
            <label id="burger" for="menu-toggle"></label>
            <ul id="menu">
            <li><Link to='/'>Home</Link></li>
            <li><Link to='/about'>About</Link></li>
            <li><Link to='/portfolio'>Portfolio</Link></li>
            <li><Link to='/contact'>Contact</Link></li>
            </ul>
        </div>
);

export default About;



